import {ADD_ITEM, DELETE_ITEM, SET_ITEMS} from '../constant';

export const addItem = (todoItem) => ({
    type: ADD_ITEM,
    payload: todoItem,
})

export const deleteItem = (id) => ({
    type: DELETE_ITEM,
    payload: id,
})

export const setItem = (obj) =>({
    type: SET_ITEMS,
    payload: obj
})